import React from "react";
import { Animation, Row, Col, Card, CardBody, Container } from "mdbreact";
import Photo from "../assets/images/web_2.png";
import SocialIcons from "../components/SocialIcons";
import { SITELINKS } from "../components/SITELINKS";
const myBlueColor = "#1d2951";
const myOrangeColor = "#ffc75f";
const About = () => {
  return (
    <section id="about" style={{ myBlueColor }}>
      <Container>
        <p className="h2-responsive font-weight-bold text-center">
          About LODESTAR!
        </p>
        <blockquote>
          We believe in the importance of human in society and from here we work
          to light your way and employ modern technology to suit your
          professional aspirations and goals of your organization. Lodestar
          believes in her ability to innovate and convey your ideas to a reality
          that serves the community and the human being.
        </blockquote>
        <Card>
          <Row>
            <Col lg="8">
              <CardBody className="form">
                <blockquote className="blockquote">
                  <h3>
                    {" "}
                    <strong style={{ color: myOrangeColor }}>Our Vision</strong>
                  </h3>
                  <p className="mb-0">
                    Contribute to the growth and expansion the vision of young
                    people to use the latest software technologies to serve the
                    human.
                  </p>
                </blockquote>
                <blockquote className="blockquote">
                  <h3>
                    <strong style={{ color: myOrangeColor }}>
                      Our Mission
                    </strong>
                  </h3>
                  <p className="mb-1">
                    Work to contribute to saving lives of people in dangerous
                    places by establishing GIS projects that serve the community
                    in addition to create a science and developmental
                    environment to build an effective generation in Arab society
                    in particular.
                  </p>
                </blockquote>
              </CardBody>
            </Col>
            <Col className="text-center" lg="4">
              <CardBody style={{ color: "#1c2a48" }} className="contact h-100">
                <Animation type="flipInY" infinite duration="4s">
                  <img src={Photo} alt="" />
                </Animation>
                <hr className="hr-light my-4" />
                <ul className="list-inline text-center list-unstyled p-2 fa-lg w-ic">
                  <SocialIcons link={SITELINKS.FACEBOOK} type="facebook" />
                  <SocialIcons link={SITELINKS.INSTAGRAM} type="instagram" />
                  <SocialIcons link={SITELINKS.LINKEDIN} type="linkedin" />
                  <SocialIcons link={SITELINKS.TWITTER} type="twitter" />
                </ul>
              </CardBody>
            </Col>
          </Row>
        </Card>
      </Container>
    </section>
  );
};

export default About;
