import React from "react";
import { Col, Container, Row, Footer } from "mdbreact";
import { SITELINKS } from "./SITELINKS";
import SocialIcons from "./SocialIcons";
import FooterLink from "./FooterLink";
const FooterPage = () => {
  return (
    <Footer color="mdb-color darken-3" className="font-small pt-4 mt-4">
      <Container fluid className="text-center text-md-left">
        <Row>
          <Col md="6" className="white-text">
            <h4 className="title" style={{ color: "#ffa000" }}>
              <strong>LODESTAR</strong>
            </h4>
            <blockquote>OUR LIGHT WILL SHINE EVERYWHERE SOON</blockquote>
          </Col>
          <Col md="6">
            <ul>
              <FooterLink name="About" link={SITELINKS.ABOUT} />
              <FooterLink name="Services" link={SITELINKS.SERVICESPAGE} />
              <FooterLink name="Projects" link={SITELINKS.PROJECTS} />
              <FooterLink name="Contact" link={SITELINKS.CONTACT} />
            </ul>
          </Col>
        </Row>
      </Container>
      <div className="footer-copyright py-3 text-center">
        <Container className="white-text">
          &copy; {new Date().getFullYear()} Copyright:{" "}
          <a href={SITELINKS.WEBSITE} target="blank">
            {" "}
            lodestar-soft.com{" "}
          </a>
          <SocialIcons link={SITELINKS.FACEBOOK} type="facebook" />
          <SocialIcons link={SITELINKS.INSTAGRAM} type="instagram" />
          <SocialIcons link={SITELINKS.LINKEDIN} type="linkedin" />
          <SocialIcons link={SITELINKS.TWITTER} type="twitter" />
        </Container>
      </div>
    </Footer>
  );
};

export default FooterPage;
